.flashcard-deck {
  width: 100%;
}

.deck-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.back-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--bg-secondary);
  color: var(--text-primary);
  padding: 0.75rem 1rem;
  border-radius: var(--radius-lg);
  font-weight: 500;
  border: 1px solid var(--border-color);
  transition: all 0.2s ease;
}

.back-btn:hover {
  background: var(--bg-card-hover);
  border-color: var(--accent-primary);
}

.deck-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  text-align: center;
  flex: 1;
}

.deck-info {
  display: flex;
  align-items: center;
}

.card-counter {
  background: rgba(99, 102, 139, 0.2);
  color: var(--accent-primary);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-lg);
  font-weight: 500;
  font-size: 0.9rem;
}

.deck-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.flashcard-container {
  width: 100%;
  max-width: 600px;
  display: flex;
  justify-content: center;
}

.card-actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  border-radius: var(--radius-lg);
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}

.hint-btn {
  background: var(--accent-warning);
  color: white;
}

.hint-btn:hover:not(:disabled) {
  background: #d97706;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.hint-btn:disabled {
  background: var(--text-muted);
  cursor: not-allowed;
  opacity: 0.6;
}

.edit-btn {
  background: var(--accent-primary);
  color: white;
}

.edit-btn:hover {
  background: #4f46e5;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.delete-btn {
  background: var(--accent-danger);
  color: white;
}

.delete-btn:hover {
  background: #dc2626;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.card-navigation {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
  justify-content: center;
}

.nav-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--bg-secondary);
  color: var(--text-primary);
  padding: 0.75rem 1.25rem;
  border-radius: var(--radius-lg);
  font-weight: 500;
  border: 1px solid var(--border-color);
  transition: all 0.2s ease;
  min-width: 120px;
  justify-content: center;
}

.nav-btn:hover:not(:disabled) {
  background: var(--bg-card-hover);
  border-color: var(--accent-primary);
  transform: translateY(-2px);
}

.nav-btn:disabled {
  background: var(--text-muted);
  cursor: not-allowed;
  opacity: 0.6;
}

.card-dots {
  display: flex;
  gap: 0.5rem;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--border-color);
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.dot:hover {
  background: var(--text-secondary);
}

.dot.active {
  background: var(--accent-primary);
  transform: scale(1.2);
}

.empty-deck {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  border: 2px dashed var(--border-color);
  margin-top: 2rem;
}

.empty-deck h3 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.empty-deck p {
  color: var(--text-secondary);
}

/* Responsive design */
@media (max-width: 768px) {
  .deck-header {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .deck-title {
    font-size: 1.75rem;
    order: -1;
  }
  
  .back-btn {
    align-self: flex-start;
  }
  
  .card-actions {
    flex-direction: column;
    width: 100%;
    max-width: 300px;
  }
  
  .action-btn {
    width: 100%;
    justify-content: center;
  }
  
  .card-navigation {
    flex-direction: column;
    gap: 1rem;
  }
  
  .nav-btn {
    min-width: 200px;
  }
}

@media (max-width: 480px) {
  .deck-title {
    font-size: 1.5rem;
  }
  
  .back-btn {
    padding: 0.5rem 0.75rem;
    font-size: 0.9rem;
  }
  
  .card-counter {
    padding: 0.25rem 0.75rem;
    font-size: 0.85rem;
  }
  
  .action-btn {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
  }
  
  .nav-btn {
    min-width: 150px;
    padding: 0.5rem 1rem;
  }
}
