.topic-panel {
  width: 100%;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.panel-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.add-topic-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-lg);
  font-weight: 500;
  font-size: 0.95rem;
  box-shadow: var(--shadow-md);
  transition: all 0.2s ease;
}

.add-topic-btn:hover {
  box-shadow: var(--shadow-lg);
  transform: translateY(-2px);
}

.btn-icon {
  width: 18px;
  height: 18px;
}

.empty-state {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  border: 2px dashed var(--border-color);
}

.empty-icon {
  width: 4rem;
  height: 4rem;
  color: var(--text-muted);
  margin-bottom: 1rem;
}

.empty-state h3 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.empty-state p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.empty-state-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--accent-primary);
  color: white;
  padding: 1rem 2rem;
  border-radius: var(--radius-lg);
  font-weight: 500;
  font-size: 1rem;
  margin: 0 auto;
}

.topics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}

.topic-card {
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  padding: 1.5rem;
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.topic-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--accent-primary), var(--accent-secondary));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.topic-card:hover::before {
  opacity: 1;
}

.topic-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.topic-name {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  flex: 1;
  margin-right: 1rem;
}

.topic-actions {
  display: flex;
  gap: 0.5rem;
}

.action-btn {
  width: 32px;
  height: 32px;
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  transition: all 0.2s ease;
}

.action-btn.edit-btn:hover {
  background: var(--accent-primary);
  color: white;
}

.action-btn.delete-btn:hover {
  background: var(--accent-danger);
  color: white;
}

.topic-description {
  color: var(--text-secondary);
  margin-bottom: 1rem;
  line-height: 1.5;
}

.topic-stats {
  margin-bottom: 1.5rem;
}

.card-count {
  background: rgba(99, 102, 139, 0.2);
  color: var(--accent-primary);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-md);
  font-size: 0.85rem;
  font-weight: 500;
}

.topic-cards {
  margin-bottom: 1.5rem;
}

.cards-preview {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.card-preview {
  background: var(--bg-secondary);
  border-radius: var(--radius-md);
  padding: 0.75rem;
  border: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}

.card-preview-text {
  color: var(--text-secondary);
  font-size: 0.9rem;
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.card-preview-actions {
  display: flex;
  gap: 0.25rem;
}

.preview-action-btn {
  width: 24px;
  height: 24px;
  border-radius: var(--radius-sm);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-muted);
  transition: all 0.2s ease;
}

.preview-action-btn:hover {
  color: var(--text-primary);
}

.more-cards {
  text-align: center;
  color: var(--text-muted);
  font-size: 0.85rem;
  font-style: italic;
  padding: 0.5rem;
}

.no-cards {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 1rem;
}

.topic-footer {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.add-card-btn, .start-revision-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  flex: 1;
  min-width: 120px;
}

.add-card-btn {
  background: var(--bg-secondary);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.add-card-btn:hover {
  background: var(--bg-card-hover);
  border-color: var(--accent-primary);
}

.start-revision-btn {
  background: var(--accent-success);
  color: white;
}

.start-revision-btn:hover:not(:disabled) {
  background: #059669;
  transform: translateY(-2px);
}

.start-revision-btn:disabled {
  background: var(--text-muted);
  cursor: not-allowed;
  opacity: 0.6;
}

/* Responsive design */
@media (max-width: 768px) {
  .panel-header {
    flex-direction: column;
    align-items: stretch;
    text-align: center;
  }
  
  .panel-title {
    font-size: 1.75rem;
  }
  
  .topics-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .topic-card {
    padding: 1.25rem;
  }
  
  .topic-footer {
    flex-direction: column;
  }
  
  .add-card-btn, .start-revision-btn {
    min-width: auto;
  }
}

@media (max-width: 480px) {
  .panel-title {
    font-size: 1.5rem;
  }
  
  .topic-card {
    padding: 1rem;
  }
  
  .topic-name {
    font-size: 1.1rem;
  }
  
  .card-preview {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
  }
  
  .card-preview-actions {
    justify-content: center;
  }
}
